

























import { Component, Prop, Vue } from "vue-property-decorator";

@Component
export default class Main extends Vue {
  @Prop() private title!: string;
  url = "";
  tags: string[] = [];
  copyBtnText = "Copy to Clipboard";

  hasUrl(): boolean {
    return this.url.trim() !== "";
  }

  onSubmit(): void {
    if (this.hasUrl()) {
      const matches =
        this.url.indexOf("youtu.be/") > -1
          ? this.url.match(/(?<=youtu.be\/)[\w-]+/g)
          : this.url.match(/(?<=v=)[\w-]+/g);

      if (matches && matches.length) {
        const id = matches[0];
        this.getVideoTags(id).then((tags) => {
          this.tags = tags;
        });
      }
    }
  }

  removeTag(index: number): void {
    this.tags.splice(index, 1);
  }

  copy(): void {
    navigator.clipboard.writeText(this.tags.join(",")).then(
      () => {
        this.copyBtnText = "Copied to Clipboard!";
        setTimeout(() => {
          this.copyBtnText = "Copy to Clipboard";
        }, 3000);
      },
      () => {
        this.copyBtnText = "Error. Try Again!";
      }
    );
  }

  clear(): void {
    this.url = "";
    this.tags = [];
    const input: HTMLElement | null = this.$el.querySelector(".url-input");
    if (input) {
      input.focus();
    }
  }

  async getVideoTags(id: string): Promise<Array<string>> {
    const endpoint = `https://www.googleapis.com/youtube/v3/videos?key=${process.env.VUE_APP_YOUTUBE_API_KEY}&fields=items(snippet(tags))&part=snippet&id=${id}`;
    return await fetch(endpoint)
      .then((res) => res.json())
      .then((data) => data.items[0].snippet.tags);
  }
}
